@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,300;0,400;0,500;1,300&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
}

body {
    font-family: 'Roboto Mono', sans-serif;
    color: #252525;
    font-weight: 400;
    background-color: #f7f2e9;
    border-bottom: 1.6rem solid #FFCC33;
    min-height: 100vh;
    padding: 3.2rem;
    padding-bottom: 6rem;
}

.container {
    max-width: 80rem;
    margin: 0 auto;

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4.8rem;
}

/* *************** */

.header {
    align-self: stretch;
}

.header h1 {
    color: #FFCC33;
    text-transform: uppercase;
    text-align: center;
    font-size: 5.2rem;
    font-weight: 300;
    letter-spacing: 3px;
    position: relative;
    width: 100%;
    display: block;
}

.header h1::before,
.header h1::after {
    display: block;
    content: '';
    height: 3px;
    width: 12rem;
    background-color: #FFCC33;
    position: absolute;
    top: calc(50% - 1px);
}

.header h1::before {
    left: 0;
}

.header h1::after {
    right: 0;
}

/* *************** */

.menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4rem;
}

.menu h2 {
    display: inline-block;
    padding: 1rem 0;
    border-top: 2px solid currentColor;
    border-bottom: 2px solid currentColor;
    font-size: 2.4rem;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-weight: 500;
    white-space: nowrap;
}

.menu > p {
    font-size: 1.5rem;
    text-align: center;
    line-height: 1.6;
    width: 80%;
}

.menu > p.message {
    width: 100%;
}

.pizzas {
    list-style: none;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 4.8rem;
}

.pizza {
    display: flex;
    gap: 3.2rem;
}

.pizza img {
    width: 12rem;
    aspect-ratio: 1;
    align-self: start;
}

.pizza div {
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
    padding: 0.4rem 0;
}

.pizza h3 {
    font-size: 2rem;
    font-weight: 400;
}

.pizza p {
    font-size: 1.4rem;
    font-weight: 300;
    font-style: italic;
    margin-bottom: auto;
}

.pizza span {
    display: block;
    font-size: 1.6rem;
}

.pizza.sold-out {
    color: #888;
}

.pizza.sold-out img {
    filter: grayscale();
    opacity: 0.8;
}

.underlined {
    border-bottom: #FFCC33 3px solid;
}

/* *************** */

.footer {
    font-size: 1.4rem;
}

.order {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2.4rem;
}

.btn {
    color: inherit;
    font-family: inherit;
    border: none;
    font-size: 1.4rem;
    font-weight: 500;
    background-color: #FFCC33;
    padding: 1.4rem 3.2rem;
    cursor: pointer;
    transition: all 0.2s;
}

.btn:hover {
    background-color: #e9bb24;
}

@media only screen and (max-width: 61.25em) {
    body {
        padding: 2.4rem;
    }

    .header h1 {
        font-size: 4.6rem;
    }

    .header h1::before, .header h1::after {
        display: none;
    }

    .menu > p {
        width: 100%;
    }

    .pizzas {
        grid-template-columns: 1fr;
        gap: 4rem;
    }

    .pizza img {
        align-self: center;
    }

    .footer p {
        text-align: center;
        line-height: 1.6;
    }
}

/*
SPACING SYSTEM (px)
2 / 4 / 8 / 12 / 16 / 24 / 32 / 40 / 48 / 64 / 80 / 96 / 128

FONT SIZE SYSTEM (px)
